<template>
    <div class="collapse is-fullwidth">
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name : 'c-collapse',
        props: {
            isFullwidth: Boolean,
            accordion  : Boolean
        },

        computed: {
            $collapseItems () {
                return this.$children.filter(child => child._isCollapseItem)
            }
        },

        methods: {
            openByIndex (index) {
                if ( this.accordion ) {
                    this.$collapseItems.forEach((item, i) => {
                        if ( i !== index ) {
                            item.isActived = false
                        }
                    })
                }
            }
        }

    }
</script>

<style>
    .collapse.is-fullwidth {
        width: 100%;
    }
</style>



